body {
  height: 100vh;
  background-color: #292626;
}
.main_page {
  width: 100%;
  height: auto;
  background-color: #292626;
}

.form {
  width: 100%;
  height: auto;
  padding: 20px 20px 0 20px;
}

.search__input {
  height: 30px;
  border: none;
  padding-left: 10px;
}

.button {
  color: white;
  text-shadow: 0px 1px gray;
  margin-left: 10px;
  padding: 0 10px;
  background-color: #f81707;
  border-radius: 5px;
  border: none;
  height: 30px;
  cursor: pointer;
  &:hover {
    box-shadow: #f81707;
    transform: scale(1.1);
  }
}

.main__container {
  display: flex;
  flex-direction: row;
}

.favorites {
  width: 30%;
  height: 100vh;
  margin: 30px 0 80px 20px;
  background-color: white;
  text-align: center;
  padding: 20px 10px 20px 10px;
  font-weight: 800;
  font-size: 20px;
  &__fav-title {
    color: #f81707;
    font-family: 'Antonio', sans-serif;
    font-weight: 800;
    letter-spacing: 0.5 px;
  }
}

.main__list {
  width: 70%;
  height: auto;
  padding: 30px 0 80px 30px;
}
.reset__btn {
  margin-top: 30px;
  background-color: #f81707;
  border-radius: 5px;
  border: none;
  height: 30px;
  width: 60px;
  cursor: pointer;
  color: white;
  text-shadow: 0px 1px gray;
  &:hover {
    box-shadow: 1px 1px 1px gray;
    transform: scale(1.1);
  }
}

.serie {
  &__box {
    list-style: none;
    padding: 10px;
    width: fit-content;
    margin-bottom: 5px;
    margin-left: -5px;
  }

  &__name {
    color: whitesmoke;
    text-transform: uppercase;
    font-family: 'Antonio', sans-serif;
    padding: 5px;
  }
}

.fav-picked .border-show {
  border-color: whitesmoke;
  margin-bottom: 1rem;
}
